.block {
  width: 32%;
  border: 1px solid #abb2bf;
  margin-bottom: 50px;
  display: flex;
  flex-direction: column;
  .languages {
    padding: 3px 5px;
    border-top: 1px solid #abb2bf;
    border-bottom: 1px solid #abb2bf;
  }
  div {
    position: relative;
    display: flex;
    flex-direction: column;
    margin-bottom: 10px;
    padding: 10px;
    p {
      margin: 5px 0 20px;
      color: #abb2bf;
    }
    a {
      border: 1px solid #ff33cc;
      width: 30%;
      text-align: center;
      padding: 5px 10px;
      transition: 0.3s linear;
    }
    a:hover {
      background-color: #cc38a7;
    }
  }
}
