.main {
  margin: 50px 0;
}
.name {
  margin-top: 50px;
  //   margin: 50px 0 px;
  display: flex;
  align-items: center;
  gap: 20px;
  h3 {
    font-size: 30px;
    font-weight: 500;
  }
  div {
    height: 1px;
    width: 300px;
    background-color: #ff33cc;
  }
}
.content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.left {
  width: 70%;
  ul {
    display: flex;
    gap: 15px;
    flex-wrap: wrap;
    li {
      padding: 10px;
      border: 1px solid #fff;
    }
  }
}
