.block {
  border: 1px solid #abb2bf;
  img {
    width: 330.579px;
    height: 201px;
    opacity: 0.7;
    transition: 0.3s linear;
  }
  img:hover {
    opacity: 1;
  }
  .languages {
    margin-top: -5px;
    padding: 3px;
    border-top: 1px solid #abb2bf;
    border-bottom: 1px solid #abb2bf;
  }
  div {
    margin-bottom: 10px;
    padding: 10px;
    p {
      margin: 5px 0 20px;
      color: #abb2bf;
    }
    a {
      border: 1px solid #ff33cc;
      width: 30%;
      text-align: center;
      padding: 5px 10px;
      transition: 0.3s linear;
    }
    a:hover {
      background-color: #cc38a7;
    }
  }
}
